import { Vec3, Node } from "cc";
import { Observable } from "../Runtime/Observer";

export interface JoystickParts {
    ring: Node;
    stick: Node;
}

export interface JoystickEvent {
    type: "start" | "end";
    direction: Vec3;
    parts: JoystickParts;
}

export class JoystickObservable extends Observable<JoystickEvent> {
    notifyStart(direction: Vec3, parts: JoystickParts): void {
        this.next({ type: "start", direction, parts });
    }

    notifyEnd(direction: Vec3, parts: JoystickParts): void {
        this.next({ type: "end", direction, parts });
    }
}
